// Footer.jsx

import React from 'react';

const Footer = () => {
  return (
    <footer className="bg-white text-gray-600 py-8 px-6 shadow-lg">
      <div className="container mx-auto flex flex-col sm:flex-row justify-between items-center space-y-6 sm:space-y-0">
        
        {/* Left Section: Brand and tagline */}
        <div className="flex flex-col items-center sm:items-start">
          <span className="text-3xl font-semibold text-gray-900">Nexus</span>
          <span className="text-sm mt-1 text-gray-500">Nexus-Marketplace,Your Dashboard </span>
        </div>

        {/* Middle Section: Quick Links */}
        <div className="flex flex-col sm:flex-row items-center sm:space-x-8 text-center sm:text-left">
          <a href="/dashboard" className="hover:text-blue-500 transition duration-200">Dashboard</a>
          <a href="/settings" className="hover:text-blue-500 transition duration-200">Settings</a>
          <a href="/support" className="hover:text-blue-500 transition duration-200">Support</a>
          <a href="/logout" className="hover:text-blue-500 transition duration-200">Logout</a>
        </div>

        {/* Right Section: Copyright */}
        <div className="text-center sm:text-right text-sm text-gray-400">
          <p>&copy; 2024 Nexus. All rights reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
